.title {
    font-size: x-large;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    margin: 40px 0px;
}

.btn {
    display: inline-block;
    background: #000;
    color: #fff;
    border: none;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
    font-family: inherit;
  }